export default "import React from \"react\";\nimport classnames from \"classnames\";\n\nexport type ButtonGroupSizes = \"sm\" | \"md\" | \"lg\";\n\nexport type ButtonGroupProps = JSX.IntrinsicElements[\"div\"] & {\n    /** Display buttons vertically */\n    vertical?: boolean;\n    /** Buttons size in the group. Available sizes: \"sm\", \"md\", \"lg\" */\n    size?: ButtonGroupSizes;\n};\n\n/** Button group wrapper. Use this to group multiple buttons */\nexport const ButtonGroup: React.FC<ButtonGroupProps> = React.memo(\n    React.forwardRef(({ vertical, size, ...props }: ButtonGroupProps, ref: React.ForwardedRef<HTMLDivElement>) => (\n        <div\n            {...props}\n            ref={ref}\n            className={classnames(\n                \"rc\",\n                \"btn-group\",\n                {\n                    [`btn-group-${size}`]: size,\n                    \"btn-group-vertical\": vertical,\n                },\n                props.className\n            )}\n            role={props.role || \"group\"}\n        >\n            {props.children}\n        </div>\n    ))\n);\n";